.card-collapse {
	&__header {
		padding: 15px 10px;
		background-color: #F9FBFF;
		cursor: pointer;

		.collapse-icon {
			&__plus {
				display: none;
			}
			&__minus {
				display: block;
			}
		}

		&.collapsed {
			.collapse-icon {
				&__plus {
					display: block;
				}
				&__minus {
					display: none;
				}
			}
		}
	}

	&__title {
		margin: 0;
		color: $default-color;
		font-family: $default-font-family-semi-bold;
		@include fluid-type(font-size, 320px, 1400px, 16px, 18px);

		display: flex;
		justify-content: space-between;
	}

	&__body {
		padding: 15px 10px;
		border-top: 2px solid #e5ecf9;
	}

	&__text {
		color: $eighth-bg-color;
		font-family: $default-font-family;
		@include fluid-type(font-size, 320px, 1400px, 14px, 16px);
	}
}
