*,
*::before,
*::after {
	box-sizing: border-box;
	outline: none;
}

html,
body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
}

body {
	background: $default-backgroung-color;
	font-family: $default-font-family;
}

section {
	padding-bottom: 50px;
}

a {
	display: inline-block;
	text-decoration: none;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 1600px;
	margin: 0 auto;

	&.breadcrumb-wrap {
		padding-top: 20px !important;
	}
}

.overlay {
	position: fixed;
	// display: none;
	top: 0;
	left: 100%;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.4);
	overflow: hidden;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	visibility: hidden;
	opacity: 0;
	z-index: 10;

	&.open {
		left: 0;
		visibility: visible;
		opacity: 1;
	}
}

.open {
	display: block !important;
}
