.simple-block {
	background: $default-backgroung-color;

	.wrapper {
		padding: 0 20px;
	}
		
	& .container-fluid, & container-lg, & .container-md, & .container-sm, & .container-xl {
		border: 1px solid $fourth-bg-color;
	}
		
	&__heading {
		padding: 25px 10px 0;

		h2 {
			margin: 0 0 10px;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 34px;
			text-transform: uppercase;
		}
	}

	&__text {
		padding: 0 10px 25px;

		p {
			max-height: 60px;
			margin: 0;
			color: $default-color;
			font-family: $default-font-family;
			@include fluid-type(font-size, 320px, 1400px, 12px, 18px);
			line-height: 28px;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
}

.bordered-block {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	margin-bottom: 15px;

	@include minw-media($breakPoints-l) {
		margin: 20px 0;
		padding: 0 10px;
	}

	& &__wrapper {
		position: relative;
		display: flex;
		flex-direction: column;
		align-self: stretch;
		justify-content: center;
		width: 100%;
		max-width: 232px;
		padding: 20px;
		background: transparent;
		background-clip: padding-box;
		border: 4px dashed $eleventh-bg-color;
		border-radius: 15px;
		text-align: center;
		z-index: 1;

		&:before {
			content: '';
			position: absolute;
			top: 0; right: 0; bottom: 0; left: 0;
			z-index: -1;
			margin: -4px; /* !importanté */
			border-radius: inherit; /* !importanté */	
		}

		&.top-border::before {
			background: linear-gradient(0deg, transparent 50%, $seventh-bg-color 50%);
		}

		&.bottom-border::before {
			background: linear-gradient(180deg, transparent 50%, $seventh-bg-color 50%);
		}

		&::after {
			content: "";
			position: absolute;
			top: 0px;
			left: 0px;
			display: block;
			width: 100%;
			height: 100%;
			background: #fff;
			z-index: -1;
			border-radius: 15px;
		}
	}

	& &__content {
		display: flex;
		flex-direction: column;
		align-self: stretch;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		padding: 10px;
		background: $fifth-bg-color;

		& h2 {
			display: inline-block;
			width: 100%;
			max-width: 66px;
			margin: 0 auto 20px;
			padding-bottom: 10px;
			color: $fourth-color;
			border-bottom: 4px solid $seventh-bg-color;
			font-family: $default-font-family-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 25px;
			text-transform: uppercase;
		}

		& p {
			flex: 1;
			display: inline-block;
			width: 100%;
			max-width: 108px;
			margin: 0 auto;
			color: $second-color;
			font-family: $default-font-family-bold;
			@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
			line-height: 20px;
			text-transform: uppercase;
		}

		& a {
			width: 100%;
			max-width: 141px;
			height: 33px;
			margin-top: 20px;
			border: 1px solid $tenth-bg-color;
			color: $default-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
			line-height: 30px;
			text-transform: uppercase;

			&:hover, &:focus, &.active {
				color: $default-color;
				text-decoration: none;
			}
		}
	}

	& &__left-arrow {
		position: relative;
		display: inline-block;
		width: 100%;
		max-width: 40px;
		height: 4px;
		z-index: 1;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: -4px;
			display: block;
			width: 80%;
			height: 4px;
			background-color: $seventh-bg-color;
		}

		&::after {
			content: "";
			position: absolute;
			top: -8px;
			right: 0;
			display: block;
			width: 20px;
			height: 20px;
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			border-left: 20px solid $seventh-bg-color;
		}
	}
}

.stars-wrapper {
	display: flex;
	align-items: flex-start;
	flex-flow: wrap;
	justify-content: space-between;
	margin-top: 20px;

	@include maxw-media ($breakPoints-l) {
		margin-top: 30px;
	}

	@include minw-media($breakPoints-l) {
		padding: 0 10px;
	}

	.block-stars__wrapper {
		margin-bottom: 20px;

		@include minw-media ($breakPoints-l) {
			margin-bottom: 10%;
		}

		& + .block-stars__wrapper {
			margin-top: 0;
		}
	}
}