footer {
	position: relative;
	background: $default-backgroung-color;
	z-index: 2;
}

.first-footer {
	&__wrapper {
		max-width: calc(100% - 40px);
		border: 1px solid $eighth-bg-color;
		margin: 0 20px 20px;
		padding: 30px;

		@include minw-media($breakPoints-m) {
			max-width: calc(50% - 30px);
			margin: 0 0 20px 20px;
		}

		@include minw-media($breakPoints-xl) {
			max-width: calc(25% - 25px);
			margin: 0 0 0 20px;
		}
	}

	&__img {
		display: inline-block;
		height: 100%;
		max-height: 60px;
		margin-bottom: 20px;
	}

	&__heading {
		margin-bottom: 20px;
		color: $second-color;
		text-align: left;
		font-family: $default-font-family-bold;
		@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
		line-height: 21px;
		text-transform: uppercase;
	}

	&__navivation {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		max-height: 115px;
		margin: 0;
		padding: 0;
		
		li {
			display: inline-block;
			max-width: 50%;

			&:nth-child(-n+4) {
				margin-right: 10px;
			}

			a {
				max-width: 100%;
				color: $eighth-color;
				text-align: left;
				font-family: $default-font-family;
				@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
				line-height: 21px;
				text-decoration: underline;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				
				&:hover, &:focus, &.active {
					color: $default-color;
				}
			}
		}
	}
}

.second-footer {
	padding: 40px 0 0;

	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		max-width: 1540px;
		margin: 0 auto;
		padding-bottom: 40px;
		border-bottom: 1px solid $ninth-bg-color;
	}

	&__heading {
		margin-bottom: 20px;
		color: $second-color;
		text-align: left;
		font-family: $default-font-family-bold;
		@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
		line-height: 21px;
		text-transform: uppercase;
	}

	&__navigation {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-flow: wrap;
		
		a {
			color: $eighth-color;
			text-align: left;
			font-family: $default-font-family;
			@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
			line-height: 21px;
			text-decoration: underline;

			& + a {
				margin-left: 20px;
			}
			
			&:hover, &:focus, &.active {
				color: $default-color;
			}
		}
	}
}

.third-footer {
	padding: 40px 0 0;

	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		max-width: 1540px;
		margin: 0 auto;
		padding-bottom: 40px;
	}

	&__first-text {
		width: 100%;
		max-width: 1320px;
		margin: 0 auto 35px;
		text-align: center;
		color: $eighth-color;
		font-family: $default-font-family;
		@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
		line-height: 24px;
	}

	&__social-media {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-flow: wrap;

		a {
			& + a {
				margin-left: 10px;
			}

			img {
				display: inline-block;
				width: 100%;
				max-width: 42px;
			}
		}
	}

	&__second-text {
		width: 100%;
		max-width: 1320px;
		margin: 0 auto 35px;
		text-align: center;
		color: $default-color;
		font-family: $default-font-family;
		@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
		line-height: 40px;
	}
}