.breadcrumb {
	margin-top: 0;
	margin-right: 0;
	margin-bottom: 0;
	margin-left: 0;
	padding-top: 0;
	padding-bottom: 0;
	background-color: transparent;

	.breadcrumb-item,
	.breadcrumb-item a{
		text-align: left;
		color: #AEAEAE;
		font-family: $default-font-family-semi-bold;
		@include fluid-type(font-size, 320px, 1400px, 12px, 14px);

		&.active {
			color: #949DAE;
		}
	}

	&-item + &-item:before {
		content: '';
		width: 26px;
		height: 8px;
		background: url('<?php echo SITEROOT; ?>images/left-arrow-gray.svg');
		background-size: contain;
		background-position: 50%;
		background-repeat: no-repeat;
		padding: 0;
	}
}

