.five-elements-block {
	background: $default-backgroung-color;

	.row + .row {
		padding: 0 15px;
	}

	&__heading {
		.col-12 {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		
			h2 {
				margin: 0 0 35px;
				color: $second-color;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
				line-height: 34px;
				text-transform: uppercase;
			}
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
	}

	&__wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		flex-wrap: wrap;
		width: 100%;

		@include minw-media(1600px) {
			justify-content: center;
		}

		&--item {
			flex: 0 0 285px;
			max-width: 285px;
			margin-top: 15px;
			margin-right: 15px;
			border: 1px solid $eighth-bg-color;

			&:last-child {
				margin-right: 0;
			}

			img {
				display: block;
				width: 100%;
			}

			p {
				height: 19px;
				margin: 15px 0 0 10px;
				color: $second-color;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
				line-height: 19px;
				text-transform: uppercase;
				overflow: hidden;
				text-overflow: ellipsis;

				& + p {
					margin: 0 0 15px 10px;
				}
			}

			a {	
				&:hover, &:focus, &.active {
					text-decoration: none;
				}
			}
		}
	}
}


.product-detail {
	.five-elements-block {
		a {
			display: block;
		}
		@include media-breakpoint-down(md) {
			[class^='col'] {
				min-width: 280px;
			}
		}

			&__wrapper--item {
				flex: auto;
				max-width: 100%;
			}
	}
}
