.second-block {
	background: $default-backgroung-color;

	.wrapper {
		padding: 50px 20px 0;
	}

	.container-fluid, .container-lg, .container-md, .container-sm, .container-xl {
		border: 1px solid $fourth-bg-color;
	}

	&__first {
		display: flex;
		flex-direction: column;
		align-items: stretch;
		justify-content: flex-start;
		width: 100%;
		padding: 25px;

		&--heading {
			margin-bottom: 30px;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 34px;
			text-transform: uppercase;

			&__subtitle {
				margin-bottom: 25px;
				color: $default-color;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 12px, 14px);
				line-height: 24px;
			}
		}

		&--description {
			width: 100%;
			height: 90px;
			margin-bottom: 20px;
			color: $default-color;
			font-family: $default-font-family;
			@include fluid-type(font-size, 320px, 1410px, 12px, 18px);
			line-height: 28px;
			overflow: hidden;
			text-overflow: ellipsis
		}

		&--wrapper {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 100%;
			margin-top: 30px;

			@include between-width-media($breakPoints-m, $breakPoints-l) {
				flex-direction: row;
				justify-content: space-between;
			}

			@include minw-media($breakPoints-xxl) {
				flex-direction: row;
				justify-content: space-between;
			}
		}

		&--video {
			img {
				display: inline-block;
				width: 100%;
			}
		}

		&--comments {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			margin-top: 30px;

			@include between-width-media($breakPoints-m, $breakPoints-l) {
				margin-top: 0;
				margin-left: 10px;
			}

			@include minw-media($breakPoints-xxl) {
				margin-top: 0;
				margin-left: 15px;
			}
		}

		&--stars {
			&-wrapper {
				display: flex;
				align-items: flex-start;
				justify-content: flex-start;

				& + .second-block__first--stars-wrapper {
					margin-top: 20px;
				}
			}

			&-text {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: flex-start;
				width: 100%;
				margin-left: 10px;

				p {
					margin: 0;
					color: $default-color;
					font-family: $default-font-family-semi-bold;
					@include fluid-type(font-size, 320px, 1400px, 11px, 17px);

					&.first-text {
						color: $second-color;
						font-family: $default-font-family;
						@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
					}
				}
			}
		}
	}

	&__second {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;
		padding: 25px;
		background: $fifth-bg-color;

		&--wrapper {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 100%;
			height: 100%;
			padding: 20px;
			background: $third-bg-color;
			border: 4px solid $sisth-bg-color;
		}

		&--subheading {
			margin: 0;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 30px, 36px);
			text-transform: uppercase;

			& span {
				color: $fourth-color;
				font-family: $default-font-family-black;
			}
		}

		&--heading {
			margin: 15px 0;
			padding: 5px 0 10px;
			border-top: 10px solid $seventh-bg-color;
			border-bottom: 10px solid $seventh-bg-color;
			color: $second-color;
			font-family: $default-font-family-black;
			@include fluid-type(font-size, 320px, 1400px, 34px, 40px);
			text-transform: uppercase;
			letter-spacing: 1vw;

			@include minw-media ($breakPoints-xxxl) {
				letter-spacing: 15px;
			}
		}

		&--first-img, &--second-img {
			display: inline-block;
			max-width: 95px;
			margin-bottom: 15px;
		}

		&--first-img {
			max-width: 95px;
			margin-bottom: 15px;
		}

		&--second-img {
			max-width: 54px;
			margin-bottom: 15px;
		}

		&--first-text {
			width: 100%;
			max-width: 500px;
			margin-bottom: 15px;
			color: $second-color;
			text-align: center;
			font-family: $default-font-family-bold;
			@include fluid-type(font-size, 320px, 1400px, 20px, 26px);
			text-transform: uppercase;
			line-height: 30px;
		}

		&--second-text {
			width: 100%;
			max-width: 590px;
			margin: 0;
			color: $second-color;
			text-align: center;
			font-family: $default-font-family-black;
			@include fluid-type(font-size, 320px, 1400px, 16px, 22px);
			text-transform: uppercase;
			line-height: 25px;
		}

		&--third-text {
			width: 100%;
			max-width: 590px;
			margin-bottom: 30px;
			color: $second-color;
			text-align: center;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
			text-transform: uppercase;
			line-height: 25px;
		}

		&--button {
			display: flex;
			align-items: center;
			justify-content: space-around;
			width: 100%;
			max-width: 286px;
			height: 50px;
			background: $seventh-bg-color;
			color: $first-color;
			text-align: center;
			font-family: $default-font-family;
			@include fluid-type(font-size, 320px, 1400px, 12px, 18px);
			text-transform: uppercase;
			line-height: 50px;

			&:hover, &:focus, &.active {
				color: $first-color;
				text-decoration: none;
			}
		}
	}
}

.product-detail {
	.second-block .container-fluid {
		border: none;
	}

	.second-block {
		.wrapper {
			padding: 50px 0	 0;
		}
	}

	.second-block__first {
		padding-right: 15px;
		padding-left: 15px;
	}

	.second-block__first--description {
		text-align: left;
		color: $default-color;
		font-family: $default-font-family;
		@include fluid-type(font-size, 320px, 1400px, 14px, 16px);
	}
}

