main {
	position: relative;
	background: $default-backgroung-color;
	z-index: 2;
}

.stars-container {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 5px;

	img {
		margin-right: 5px;
	}
}

.block-stars {
	&__wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;
		max-width: 306px;

		& + .block-stars__wrapper {
			margin-top: 20px;
		}

		&--image {
			display: inline-block;
			width: 100%;
			max-width: 41px;
		}

		&--text {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 100%;
			margin-left: 10px;
	
			p {
				margin: 0;
				color: $default-color;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 11px, 17px);
	
				&.first-text {
					color: $second-color;
					font-family: $default-font-family;
					@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
				}
			}
		}
	}
}

.link-button {
	display: inline-flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	max-width: 221px;
	height: 38px;
	border: 1px solid $ninth-color;
	padding: 0 15px;
	background: $third-bg-color;
	color: $default-color;
	font-family: $default-font-family-semi-bold;
	@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
	line-height: 36px;
	text-transform: uppercase;

	&:hover, &:focus, &.active {
		color: $default-color;
		text-decoration: none;
	}

	img {
		display: inline-block !important;
		width: auto !important;
	}


	&.link-button--sm {
		height: 30px;
		line-height: 24px;
	}
}

