.first-fixed-block {
	padding-top: 150px;

	@include minw-media ($breakPoints-l){
		position: fixed;
		top: 150px;
		width: 100%;
		padding-top: 0;
		z-index: 1;
	}

	&__img-group {
		position: relative;
		padding: 0;

		&--img {
			display: block;
			width: 100%;
			max-height: 720px;
		}

		&--text {
			position: absolute;
			top: calc(50% - 85px);
			width: 100%;
			height: 190px;

			@include maxw-media ($breakPoints-m){
				top: initial;
				bottom: 10px;
				height: initial;
			}

			.wrapper {
				max-width: 1110px;
				margin-right: 0;
				padding: 0 15px;
			}

			& p {
				width: 100%;
				max-width: 600px;
				margin-bottom: 1vw;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 12px, 20px);
				// line-height: 26px;
				text-transform: uppercase;

				&.first-text {
					color: $second-color;
				}

				&.second-text {
					color: $third-color;
				}

				&.third-text {
					color: $fourth-color;
				}
			}

			.link-button {
				font-family: $default-font-family-bold;
			}
		}
	}

	&__text-group {
		display: flex;
		align-items: center;
		justify-content: center;

		@include maxw-media($breakPoints-l) {
			margin-top: 30px;
		}

		&--wrapper {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 100%;
			max-width: 400px;
		}

		&--items {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			width: 100%;
			padding: 0 0 15px 0;
			border-bottom: 1px solid $second-bg-color;

			& + .first-fixed-block__text-group--items {
				margin-top: 30px;
			}

			img {
				display: inline-block;
			}

			p {
				margin: 0;
				color: $second-color;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 20px, 26px);
				line-height: 34px;
				text-transform: uppercase;

				& + p {
					color: $default-color;
					font-family: $default-font-family-semi-bold;
					@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
					line-height: 22px;
					text-transform: uppercase;
				}

				span.first-text {
					color: $fifth-color;
				}

				span.second-text {
					color: $sixth-color;
				}

				&.first-text {
					color: $seventh-color;
					font-family: $default-font-family-semi-bold;
					@include fluid-type(font-size, 320px, 1400px, 8px, 14px);
					line-height: 20px;
					text-transform: uppercase;
				}
			}
		}

		&--text {
			flex: 1;
			margin: 0 15px;
		}
	}
}

.product-detail {

	.first-fixed-block {
		background: #f9fbff;
	}

	.first-fixed-block__img-group--text {
		top: calc(50% - 65px);

		.hero-title {
			text-align: left;
			font-family: $default-font-family-black;
			@include fluid-type(font-size, 320px, 1400px, 32px, 34px);
			color: $second-color;
			text-transform: uppercase;
		}

		.hero-p {
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 16px, 18px);
			text-transform: uppercase;
		}

		.wrapper {
			max-width: 1277px;
		}
	}


	.first-fixed-block__text-group--wrapper {
		max-width: 100%;
		padding-left: 40px;
		padding-right: 40px;
	}

	.first-fixed-block__text-group {
		.second-block__first--heading {
			margin-bottom: 20px;
		}
	}

	.list-default {
		margin-bottom: 2rem;
	}
}
