.company-block {
	background: $default-backgroung-color;
	
	.wrapper {
		max-width: 1760px;
		margin-left: 0;

		@include minw-media($breakPoints-xxxl){
			max-width: 88vw;
		}
	}

	.col-lg-3 {
		@include minw-media($breakPoints-l){
			flex: 0 0 30%;
			max-width: 30%;
			padding-left: 0;
		}
	}

	.col-lg-6 {
		@include minw-media($breakPoints-l){
			flex: 0 0 40%;
			max-width: 40%;
			padding-left: 0;
		}
	}

	&__images {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;

		img {
			display: block;
			width: 100%;
		}

		&--text {
			position: absolute;
			left: 7%;
			bottom: 7%;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			z-index: 1;

			@include maxw-media($breakPoints-l) {
				left: 10px;
				bottom: 10px;
			}

			p {
				margin: 0;
				color: $second-color;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
				line-height: 21px;
				text-transform: uppercase;
	
				& + p {
					margin: 0 0 15px 0;
				}
			}

			.link-button {
				background: transparent;
			}

			&.white-color {
				p {
					color: $first-color;
				}

				.link-button {
					border: 1px solid $default-backgroung-color;
					color: $first-color;
				}
			}
		}
	}

	&__text {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;

		@include minw-media($breakPoints-l){
			padding-left: 30px !important;
		}

		&--wrapper {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 100%;

			@include maxw-media($breakPoints-l) {
				margin-top: 50px;
			}

			& + .company-block__text--wrapper {
				margin-top: 90px;

				@include maxw-media($breakPoints-l) {
					margin-top: 50px;
				}
			}

			h2 {
				margin: 0 0 10px;
				color: $second-color;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
				line-height: 34px;
				text-transform: uppercase;
			}

			P {
				max-height: 60px;
				margin: 0 0 20px;
				color: $default-color;
				font-family: $default-font-family;
				@include fluid-type(font-size, 320px, 1400px, 12px, 18px);
				line-height: 28px;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
	}
}