.catalog-block {
	background: $default-backgroung-color;

	.wrapper {
		max-width: 1350px;
	}

	.row + .row {
		@include minw-media($breakPoints-m) {
			padding-left: 15px;
		}
	}

	&__heading {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		h2 {
			margin: 0 0 50px;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 34px;
			text-transform: uppercase;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		margin-bottom: 20px;

		@include minw-media ($breakPoints-m) {
			padding-left: 0;
		}

		@include minw-media($breakPoints-l) {
			align-items: flex-start;
			justify-content: flex-start;
		}

		img {
			display: block;
			width: 100%;

			@include minw-media($breakPoints-l) {
				max-height: 320px;
			}
		}

		p {
			margin: 15px 0 0 15px;
			color: $second-color;
			font-family: $default-font-family-bold;
			@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
			line-height: 21px;
			text-transform: uppercase;

			& + p {
				margin: 0 0 0 15px;
			}
		}

		a {
			&.link-button {
				margin: 15px 0 15px 15px;
			}

			&:hover, &:focus, &.active {
				text-decoration: none;
			}
		}
			
		&--center {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			border: 1px solid $eighth-bg-color;

			img {
				display: inline-block;
				width: auto;
				max-height: inherit;
				margin-bottom: 40px;
			}

			a.link-button {
				img {
					margin-bottom: 0;
				}
			}
		}
	}
}

