.badge {
	border-radius: 0;
	padding-right: 10px;
	padding-left: 10px;
	font-family: $default-font-family-semi-bold;
	font-size: 12px;
	color: $first-color;
	text-transform: uppercase;
	line-height: 38px;
	vertical-align: bottom;

	&.badge--sm {
		height: 30px;
		line-height: 24px;
	}

	&.in-stock {
		background-color: $seventh-color;
	}
}
