.you-design-block {
	padding-bottom: 0;

	&.we-desing-block {
		padding-bottom: 50px;

		.wrapper {
			max-width: 1760px;
			margin-left: auto;
			margin-right: 0;
		}

		.you-design-block__wrapper {
			@include minw-media($breakPoints-l) {
				padding-left: 0;
				padding-right: 45px;
			}
		}

		.you-design-block__wrapper--text-group {
			div {
				p {
					color: $sixth-color;
				}
			}
			p {
				span {
					color: $sixth-color;
				}
			}
		}
	}

	.wrapper {
		max-width: 1760px;
		margin-left: 0;
		margin-right: auto;
	}

	&__image {
		@include minw-media($breakPoints-l) {
			margin-left: -15px;
			margin-right: -15px;
		}

		img {
			display: block;
			width: 100%;
		}
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		align-self: flex-start;
		justify-content: flex-start;
		padding-top: 50px;
		width: 100%;

		@include minw-media($breakPoints-l) {
			padding-left: 45px;
		}
		
		&--heading {
			margin: 0 0 10px;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 34px;
			text-transform: uppercase;
		}

		&--text {
			margin: 0 0 80px;
			color: $default-color;
			font-family: $default-font-family;
			@include fluid-type(font-size, 320px, 1400px, 12px, 18px);
			line-height: 28px;
		}

		&--text-group {
			display: flex;
			flex-direction: column;
			align-self: flex-start;
			justify-content: flex-start;
			width: 100%;
			margin: 0 0 50px;

			div {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				width: 100%;

				img {
					display: inline-block;
					width: 100%;
					max-width: 43px;
					margin-right: 15px;
				}

				p {
					margin: 0;
					color: $fifth-color;
					font-family: $default-font-family-bold;
					@include fluid-type(font-size, 320px, 1400px, 20px, 26px);
					line-height: 34px;
					text-transform: uppercase;
				}
			}

			p {
				margin: 0;
				color: $default-color;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
				line-height: 22px;
				text-transform: uppercase;

				span {
					color: $fifth-color;
				}
			}
		}

		.block-stars__wrapper {
			margin-bottom: 50px;
		}
	}
}