header {
	position: fixed;
	top: 0;
	width: 100%;
	height: 100vh;
	max-height: 150px;
	box-shadow: 0 3px 6px rgba($color: #000000, $alpha: 0.16);
	z-index: 5;
}

.first-header {
	width: 100%;
	background: $first-header-bg-color;
	color: $first-color;
	font-family: $default-font-family-bold;
	// font-size: 16px;
	@include fluid-type(font-size, 320px, 1400px, 10px, 16px);

	p {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		margin: 0;
		padding: 8px 0;
		text-transform: uppercase;

		& + p {
			margin-left: 30px;
		}

		img {
			display: inline-block;
			margin-right: 10px;
		}
	}

	&__shipping, &__phone-card {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__shipping {
		@include minw-media($breakPoints-l) {
			justify-content: flex-start;
		}
	}

	&__phone-card {
		@include minw-media($breakPoints-l) {
			justify-content: flex-end;
		}

		&--phone, &--card {
			display: flex;
			align-items: center;
			justify-content: flex-start;
		}

			&--card {
			a {
				display: block;
				padding: 8px 0;

				& + a {
					margin-left: 20px;
				}
			}
			
			@include minw-media($breakPoints-l) {
				margin-left: 80px;
			}
		}
	}
}

.second-header {
	width: 100%;
	background: $second-header-bg-color;
	color: $second-color;
	font-family: $default-font-family-semi-bold;
	// font-size: 18px;
	@include fluid-type(font-size, 320px, 1510px, 12px, 18px);

	@include minw-media ($breakPoints-xl){
		@include fluid-type(font-size, 1200px, 1510px, 14px, 18px);
	}

	&__logo-nav {
		display: flex;
		align-items: center;
		justify-content: flex-start;

		&--logo {
			a {
				padding: 14px 0;
			}
		}

		&--navigation {
			margin: 0 auto;

			ul {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				flex-wrap: wrap;
				margin: 0;
				padding: 0;

				li {
					display: inline-block;

					& + li {
						margin-left: 15px;
					}

					a {
						padding: 2px;
						color: $second-color;
						text-transform: uppercase;

						&:hover, &:focus, &.active {
							text-decoration: none;
							color: $default-hover-color;
						}

						&.you-design {
							padding: 2px 15px;
							background: $first-backgroung-color;
							color: $first-color;
						}

						&.we-design {
							padding: 2px 15px;	
							background: $second-backgroung-color;
							color: $first-color;
						}
					}
				}
			}
		}
	}

	&__search{
		display: flex;
		align-items: center;
		justify-content: flex-end;

		button {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0;
			background: $default-backgroung-color;
			border: none;
			outline: none;
		}
	}

	& .search-form-wrapper {
		position: absolute;
		top: calc(50% - 23px);
		right: 100%;
		width: 100%;
		max-width: 450px;
		// display: none;
		-webkit-transition: all .3s ease-in-out;
		-moz-transition: all .3s ease-in-out;
		-o-transition: all .3s ease-in-out;
		transition: all .3s ease-in-out;
		opacity: 0;
		visibility: hidden;
		z-index: 11;

		form {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			width: 100%;

			input {
				width: 100%;
				height: 46px;
				padding: 10px 50px 10px 10px;
				border: none;
				box-shadow: 0 0 10px 3px hsla(0,0%,100%,.8);
			}

			button {
				position: absolute;
				top: calc(50% - 15px);
				right: 10px;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 30px;
				height: 30px;
				background: none;
				border: none;
				padding: 0;
				outline: none;
			}
		}

		&.open {
			right: 0;
			visibility: visible;
			opacity: 1;
		}
	}
}