.showroom-block {
	background: $default-backgroung-color;

	& .col-lg-3 {
		@include minw-media($breakPoints-l){
			flex: 0 0 30%;
			max-width: 30%;
			padding-left: 0;
		}
	}

	& .col-lg-6 {
		@include minw-media($breakPoints-l){
			flex: 0 0 40%;
			max-width: 40%;
			padding-left: 0;
		}
	}

	&__heading {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		
		h2 {
			margin: 0 0 50px;
			color: $second-color;
			font-family: $default-font-family-semi-bold;
			@include fluid-type(font-size, 320px, 1400px, 24px, 30px);
			line-height: 34px;
			text-transform: uppercase;
		}
	}

	&__wrapper {
		padding-left: 15px;

		& + .showroom-block__wrapper {
			margin-top: 15px;
		}
	}

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;
		width: 100%;
		height: 100%;
		cursor: pointer;

		&:hover {
			.showroom-block__item--content {
				transform: scale(1);
				visibility: visible;
			}
		}

		&--img {
			display: block;
			width: 100%;
			height: 100%;
			max-height: 653px;
		}

		&--wrapper {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			padding: 15px;
		}

		&--content {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			width: 100%;
			height: 100%;
			padding: 47px;
			background: rgba(255, 255, 255, 0.8);
			visibility: hidden;
			transform: scale(0);

			-webkit-transition: all 0.3s ease-in-out;
			-moz-transition: all 0.3s ease-in-out;
			-o-transition: all 0.3s ease-in-out;
			transition: all 0.3s ease-in-out;

			h2 {
				margin: 0 0 45px;
				color: $second-color;
				text-align: center;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 14px, 20px);
				line-height: 27px;
				text-transform: uppercase;
			}

			p {
				margin: 0 0 40px;
				color: $first-bg-color;
				text-align: center;
				font-family: $default-font-family-semi-bold;
				@include fluid-type(font-size, 320px, 1400px, 10px, 16px);
				line-height: 22px;
				text-transform: uppercase;
			}
		}

		&--images {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100%;
			max-width: 40%;
			margin-bottom: 20px;

			img {
				display: inline-block;
				margin-right: 15px;
			}

			p {
				margin: 0;
				color: $second-color;
				text-align: center;
				font-family: $default-font-family-bold;
				@include fluid-type(font-size, 320px, 1400px, 20px, 26px);
				line-height: 34px;
				text-transform: uppercase;
			}
		}
	}
}